import React from "react";
import styled from "styled-components";
import { useGetNewArivalsQuery } from "../../../redux/api/products/productsAPI";
import Product from "../../products/ProductCard";
import Pagination from "../../utils/Pagination";

const Products = ({ category, currentPage, setCurrentPage }) => {
  console.log("Products(NewArivals).js render =>");

  // Fetch New Arrivals Products
  const { isLoading, data, isError } = useGetNewArivalsQuery(
    { category, currentPage },
    {
      refetchOnMountOrArgChange: true,
      refetchOnReconnect: true,
    }
  );

  if (isLoading) {
    return <div>Loading...</div>;
  }

  if (isError) {
    return <div style={{ color: "red" }}>There was an error!</div>;
  }

  return (
    <div>
      <ProductContainer>
        <ProductBox>
          {data?.products.map((product) => {
            return <Product key={product._id} product={product} />;
          })}
        </ProductBox>
      </ProductContainer>
      {data?.pages?.length > 1 && (
        <Pagination
          currentPage={currentPage}
          setCurrentPage={setCurrentPage}
          pages={data.pages}
        />
      )}
    </div>
  );
};

export default Products;

const ProductContainer = styled.div`
  width: 100%;
`;

const ProductBox = styled.div`
  width: 100%;
  display: grid;
  gap: 1rem;
  margin: 0;
  padding: 0;
  grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
`;
